body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #ffffff; }

h1 {
  font-size: 28px; }

h2 {
  font-size: 20px; }

.button {
  background: #61dbfb;
  border: 1px solid #61dbfb;
  color: #ffffff;
  padding: 10px 15px;
  border-radius: 10px;
  font-size: 16px;
  -webkit-transition: background .25s ease, border .25s ease;
  transition: background .25s ease, border .25s ease; }
  .button + .button {
    margin-left: 10px; }
  .button:hover {
    cursor: pointer;
    background: #06bff0;
    border: 1px solid #06bff0; }

.text-link {
  border: 0;
  background: transparent;
  color: #61dbfb;
  text-decoration: underline; }
  .text-link:hover {
    color: #06bff0;
    cursor: pointer; }

.questionnaire__fields {
  text-align: left;
  margin-bottom: 30px; }

.questionnaire__navigation {
  margin-bottom: 30px; }

.questionnaire__submit {
  margin-bottom: 30px; }

.questionnaire__thanks {
  padding-bottom: 30px; }

.questionnaire__name {
  margin-bottom: 30px; }

.questionnaire__field {
  margin-bottom: 10px;
  position: relative; }
  .questionnaire__field input {
    cursor: pointer;
    position: absolute;
    opacity: 0; }
  .questionnaire__field label {
    cursor: pointer;
    position: relative; }
    .questionnaire__field label:before {
      content: '';
      margin-right: 10px;
      display: inline-block;
      vertical-align: text-top;
      width: 20px;
      height: 20px;
      background: #ffffff;
      margin-top: 3px; }
  .questionnaire__field input:checked + label:after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    background: #61dbfb;
    width: 20px;
    height: 20px;
    margin-top: 3px; }

